<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-10">
          <p class="font-semibold p-3 text-lg">Tambah data Geo JSON Peta !</p>
          <hr>
         <!-- <button type="button" class="btn btn-sm btn-style9 float-right text-xs" @click="$router.go(-1)" >{{$route.query.nama}}</button> -->
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
         <div class="sm-form ">
             <label for="title">Title : </label>
             <input type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
         </div>
         <!-- <div class="sm-form ">
             <label for="lat">Latitude : </label>
             <input type="text" id="lat" name="lat" class="form-control form-control-sm" placeholder="lat" v-model="vdata.latitude" >
         </div>
         <div class="sm-form ">
             <label for="long">Longitude : </label>
             <input type="text" id="long" name="long" class="form-control form-control-sm" placeholder="long" v-model="vdata.longitude" >
         </div> -->
         <p class="mt-1 mb-1">Kategori</p>
         <select class='form-control' v-model="vdata.kategori">
          <option>Route</option>
          <option>Signal</option>
         </select>
         <div class="sm-form ">
             <label for="upload">Upload geojson file : </label>
             <input type="file" id="filenya" name="upload" class="form-control form-control-sm" placeholder="upload" @change="upload" >
             <div class="sm-form ">
                 <input type="text" id="geojson" name="geojson" class="form-control form-control-sm" placeholder="geojson" v-model="vdata.geojson" >
             </div>
         </div>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span>  Proses {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
      </div>
    </form>
    <!-- TABLE PART -->
    <div class="row justify-content-center">
      <div class="col-md-10 sm:ml-5">
            <div v-if="datanya.length>0" class="p-3">
            <Btables :tables="datanya"  :cari="['']" :hide="['latitude','longitude']" @selected="ambil" ref="btable" />
            </div>
      </div>
    </div>
    <!-- END TABLE PART -->
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp_laravel.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let urlnya=localStorage.getItem('urlnya');
let sdb = new autophp();
export default {
  layout:'app_admin2',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
    };
  },
  methods: {
    upload(){
        let that=this;
        let file = document.querySelector("#filenya"); // berikan id pada input file
        sdb.collection('tbuser',false,urlnya).upload(file).then(res=>{
           that.vdata.geojson=res
           that.$forceUpdate();
        })
    },
    test(){
      this.$refs.btable.getData()
    },
    getData(){
      sdb.collection("app_gis_geojson",false,urlnya).doc().select(`select * from app_gis_geojson where id_peta='${this.$route.query.id}'`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
      
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    submit() {
      let that=this
        that.vdata.id_peta=this.$route.query.id
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          //idnya letak di vdata
          delete this.vdata.id;
          sdb
            .collection("app_gis_geojson",false,urlnya)
            .doc()
            .set(this.vdata)
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')

            });
        } else if (this.btn == "update") {
          sdb
            .collection("app_gis_geojson",false,urlnya)
            .doc()
            .set(this.vdata)
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')
            });
        } else if (this.btn == "delete") {
          sdb
            .collection("app_gis_geojson",false,urlnya)
            .doc(this.vdata.id)
            .delete()
            .then((res) => {
              that.getData();
            // that.$nuxt.$emit('getData')
            });
        }
        // this.$nuxt.$emit('getData','ok')
      }
    },
  },
  async mounted() {
    this.getData();
  },
};
</script>